.fullscreen {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;

  .back {
    display: flex;
    align-items: center;
    position: absolute;
    top: 10px;
    left: 10px;
    color: white;
    cursor: pointer;
    z-index: 10;

    .back-icon {
      margin-right: 10px;
    }
  }

  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
